lor3: #d65ae8;\n\t--icolor4: #63c8ff;\n}\nbody {\n    // Main toolbar\n    --bgcolor-toolbar-main: var(--color-primary-1000);\n    --bgimage-toolbar-main: linear-gradient(to bottom, var(--color-primary-900) 0%, var(--color-primary-1000) 100%);\n\n    // Cards\n    --color-card: var(--dark-custom-400);\n    --bgcolor-card: #fff;\n    --color-card-header: rgba(255, 255, 255, 0.75);\n    --bgcolor-card-header: var(--color-primary-800);\n    --bgimage-card-header: linear-gradient(to right, var(--color-primary-1000) 0, var(--color-primary-900) 50%, var(--color-primary-1000) 100%);\n    --bgcolor-card-subheader: var(--color-primary-700);\n    --bgcolor-card-footer: var(--color-primary-200);\n    --bgimage-card-footer: linear-gradient(to right, var(--color-primary-200) 0, var(--color-primary-100) 50%, var(--color-primary-200) 100%);\n\n    // Subcards\n    --color-subcard: var(--color-card);\n    --bgcolor-subcard: var(--bgcolor-card);\n    --bordercolor-subcard: var(--color-primary-100);\n    --color-subcard-header: inherit;\n    --bgcolor-subcard-header: var(--color-primary-50);\n    --bordercolor-subcard-header: var(--bordercolor-subcard);\n\n    // Buttons\n    --color-button-secondary: rgba(255, 255, 255, 0.875);\n    --bgcolor-button-secondary: var(--color-secondary-500);\n    --bordercolor-button-secondary: rgba(0, 0, 0, 0.125);\n    --color-button-secondary-hover: #000;\n    --bgcolor-button-secondary-hover: var(--color-secondary-400);\n    --transition-button: color 0.33s ease, background-color 0.33s ease;\n\n    // Accordions\n    --bgcolor-accordion: var(--color-primary-75);\n    --bordercolor-accordion: rgba(0, 0, 0, 0.25);\n\n    // Modals\n    --color-modal-header: var(--color-card-header);\n    --bgcolor-modal-header: var(--bgcolor-card-header);\n    --color-modal-content: var(--color-card);\n    --bgcolor-modal-content: var(--bgcolor-card);\n\n    // Sidebar\n    --color-sidebar: inherit;\n    --bgcolor-sidebar: #e3e3e3;\n    --color-sidebar-header: var(--c